import React, { useState } from 'react';
import { SenseRevision, CwnSense, SenseTree } from '../datatypes';
import { Stack, IconButton, Announced, Rating } from '@fluentui/react';
import { SensePicker } from './SensePicker';
import { parseFilter } from './sorter_utils';

export interface IRevisionViewProps {
  revision: SenseRevision | null;
  senses: CwnSense[],
  senseTree: SenseTree|null,
  onSenseRevisionUpdated: (revision: SenseRevision) => void;
}

export function RevisionView(props: IRevisionViewProps) {
  let revision = props.revision;
  let iconStyle = { height: "30px", width: "30px", fontSize: "100px" };
  const [isAmbig, setIsAmbig] = useState(false);
  const [rating, setRating] = useState(3);

  if (revision) {

    const onFilterSenses = (filter: string) => {
      return parseFilter(filter, props.senses, props.senseTree);
    }

    const onRevisionUpdated = (newRevision: SenseRevision) => {
      props.onSenseRevisionUpdated(newRevision);
    }

    const onSenseCandidatesUpdated = (newCandidates: string[]) => {
      let newRevision = Object.assign({}, revision);
      newRevision.candidates = newCandidates;
      newRevision.timestamp = Date.now();
      onRevisionUpdated(newRevision);
    }

    const onRatingChanged = (score: number) => {
      setRating(score);
    }

    const toggleIsAmbig = () => { setIsAmbig(!isAmbig) };

    let selectedSenses: CwnSense[] = [];
    if (props.revision) {
      selectedSenses = props.revision.candidates.map((x) => {
        let targets = props.senses.filter((sense) => sense.cwn_id == x);
        return (targets && targets.length > 0) ? targets[0] : null;
      }).filter((x) => x !== null) as CwnSense[];
    }

    const ratingElem = isAmbig ? (
      <Stack.Item styles={{ root: { width: "120px" } }}><Rating
        title="15"
        min={1}
        max={5}
        rating={rating}
        onChange={(ev, value) => value ? onRatingChanged(value) : null}
        icon="CircleFill"
        unselectedIcon="CircleRing"
      /></Stack.Item>) : null;

    return (
      <Stack horizontal styles={{ root: { width: "100%;" } }}>
        <Stack.Item grow={1}>
          <SensePicker
            senseCandidates={selectedSenses}
            onPickerChanged={onSenseCandidatesUpdated}
            onSenseSuggestions={onFilterSenses}
          />
        </Stack.Item>
        {ratingElem}
        <Stack.Item styles={{ root: { width: "100px" } }}>
          <Stack tokens={{ childrenGap: 2 }} horizontal horizontalAlign="end">
            <IconButton style={iconStyle}
              iconProps={{ iconName: 'CheckMark' }}
              title="" ariaLabel="CheckMark"
            />
            <IconButton style={iconStyle}
              iconProps={{ iconName: 'Communications' }}
              title="" ariaLabel="Communications"
              onClick={toggleIsAmbig} />
          </Stack>
        </Stack.Item>
      </Stack>
    )
  } else {
    return null;
  }
}