import { SenseAnnotSession, User, Assignments, Trial, Annotations, SenseRevision, Annotation } from "../datatypes";
import { fetchSenses, fetchSession, fetchAnnotations } from '../DataHandler';
import { fetchAssignments, fetchSentence } from '../DataHandler';

type StateSetter<T> = React.Dispatch<React.SetStateAction<T>>
export async function updateSession(
  user: User | null,
  session_setter: StateSetter<SenseAnnotSession | null>) {

  let new_session: SenseAnnotSession | null = null;
  
  if (user !== null) {
    try {
      new_session = await fetchSession(user);
      console.debug(new_session);
    } catch (err) {
      console.error(err);      
    }

    if (new_session === null){
      new_session = {
        userId: user.uid,
        userEmail: user.email,
        lastActivity: Date.now(),
        currentAssignmentId: "",    
        currentItemId: ""
      };
    }
  } else {
    new_session = null;
  }

  session_setter(new_session);
}

export async function updateAssignments(
  user: User | null,
  assignments_setter: StateSetter<Assignments>) {

  let assignments: Assignments = [];
  if (user !== null) {
    try {
      assignments = await fetchAssignments(user);      
    } catch (err) {
      console.error(err);      
      assignments = [];
    }
  } else {
    assignments = [];
  }

  assignments_setter(assignments);
}


export async function updateAnnotations(
  user: User | null,
  annotations_setter: StateSetter<Annotations>) {

  let annotations = {} as Annotations;
  if (user !== null) {
    try {
      annotations = await fetchAnnotations(user);
    } catch (err) {
      console.error(err);
    }
  } else {
    // pass
  }

  annotations_setter(annotations);
}

export async function updateTrial(
  session: SenseAnnotSession | null,
  trial_setter: StateSetter<Trial>) {

  let trial: Trial = {} as Trial;

  if (session && session.currentItemId.length > 0) {
    let itemId = session.currentItemId;
    let assignId = session.currentAssignmentId;

    let sentenceId = itemId.split(":")[0];
    let tokenId = parseInt(itemId.split(":")[1]);
    let sentence = await fetchSentence(sentenceId);
    try {
      trial = {
        sentenceId: sentenceId,
        sentence: sentence,
        token: sentence[tokenId],
        userEmail: session.userEmail,
        userId: session.userId, 
        assignId: assignId,
        itemId: itemId
      }
    } catch (err) {
      console.error(err);
    }
  } else {
    // pass
  }

  trial_setter(trial);
}

export function makeNewSession(
  session: SenseAnnotSession,
  assignId: string = "", itemId: string = "") {
  
  let newSession = Object.assign({}, session);
  if (assignId.length > 0){
    newSession.currentAssignmentId = assignId;  
  }

  if(itemId.length > 0){
    newSession.currentItemId = itemId;
  }
  
  newSession.lastActivity = Date.now();
  
  return newSession;
}

export function makeNewAnnotation(
  assignId: string, session: SenseAnnotSession){
  
  let userEmail = session.userEmail;
  let annot = {
    annotId: `${userEmail}:${assignId}`,
    annotatorId: session.userId,
    assignId: assignId,
    results: {} as {[key: string]: SenseRevision}
  } as Annotation;
  return annot;
}
