import React from 'react';
import { PretaggedToken } from '../datatypes';
import { Token } from './Token';
import { Stack } from '@fluentui/react';

export interface ISentenceViewerData {
  sentence: PretaggedToken[];
  itemId: string;
}

function SentenceViewer(props: ISentenceViewerData) {
  let sentence = props.sentence;
  let itemId = props.itemId;

  if (sentence === undefined) {
    return null;
  } else {
    let tokenId = -1;
    try {
      tokenId = parseInt(itemId.split(":")[1]);
    } catch (err) {
      //pass
    }

    return (
      <div className="row">
        <div className="mx-auto mt-4">
          <div className="card-body bg-light p-3 mb-3 border border-primary rounded-lg">
            <Stack horizontal tokens={{ childrenGap: 3 }}>
              {sentence.map((token, index) => {
                let isFocus = tokenId == index;
                return (<Token token={token} isFocus={isFocus}></Token>)
              })}
            </Stack>
          </div>
        </div>
      </div>
    )
  }
}

export default SentenceViewer;