import React from 'react';
import { Annotations, Assignments } from '../datatypes';
import { List, ActionButton, Stack, concatStyleSetsWithProps } from '@fluentui/react';
import { ITaskItem } from './task_utils';

export interface IAssignmentListProps {
  items: ITaskItem[]
  onAssignIdChanged: (assignId: string) => void;
}

export function AssignmentList(props: IAssignmentListProps) {
  function render_task_item(item: ITaskItem | undefined, index: number | undefined) {

    let actStyle = {
      root: { padding: "1px", height: "24pt" },
      icon: { color: "white" },
      iconHovered: { color: "white" },
      rootHovered: {
        border: "1px solid white",
        borderRadius: "5px"
      }
    };

    if (item) {

      let check_button = item.checked ?
        (<ActionButton styles={actStyle} iconProps={{ iconName: "MSNVideosSolid" }} checked={false} />) :
        (<ActionButton styles={actStyle}
          onClick={()=>props.onAssignIdChanged(item.name)}
          iconProps={{ iconName: "MSNVideos" }}
          checked={false} />);

      return (
        <Stack horizontal>
          <Stack.Item align="center">{check_button}</Stack.Item>
          <Stack.Item grow={1} align="center">{item.name}</Stack.Item>
          <Stack.Item align="center">{item.nDone}/{item.nAmbig}/{item.nTotal}</Stack.Item>
        </Stack>
      )
    } else {
      return null;
    }
  }
  return (
    <List items={props.items} onRenderCell={render_task_item}></List>
  );
}


