import React, { useState, useEffect } from 'react';
import { Assignments, Annotations, SenseAnnotSession } from '../datatypes';
import { List, Stack, IconButton, ProgressIndicator, Button, ActionButton, Label, TextField, Separator } from '@fluentui/react';
import { white } from 'color-name';
import { populate_task_items, compute_progress } from './task_utils';
import { AssignmentList } from './AssignmentList';
import { NavigationBar } from './NavigationBar';
import { findCurrentAssignment, findCurrentItemNo } from './controller_utils';
import { makeNewSession } from '../SenseAnnot/annot_updater';


export interface ITasksProps {
  session: SenseAnnotSession | null;
  assignments: Assignments;
  annotations: Annotations;
  onAssignIdChanged: (assignId: string) => void;
  onItemIdChanged: (itemId: string) => void;
}

export function Tasks(props: ITasksProps) {
  let assignments = props.assignments;
  let annotations = props.annotations;
  let session = props.session;

  const assig = findCurrentAssignment(props.session, props.assignments);
  let itemNo = findCurrentItemNo(props.session, assig);

  if (session === null) {
    return null;
  }

  let assign_items = populate_task_items(
    session.currentAssignmentId, props.assignments, props.annotations);
  let progress = compute_progress(assign_items);

  const onNextTrial = () => {
    const assig = findCurrentAssignment(props.session, props.assignments);
    let itemNo = findCurrentItemNo(props.session, assig);
    onItemNoUpdated(itemNo + 1);
  }

  const onPrevTrial = () => {
    const assig = findCurrentAssignment(props.session, props.assignments);
    let itemNo = findCurrentItemNo(props.session, assig);
    onItemNoUpdated(itemNo - 1);
  }

  // on item number changed
  const onItemNoUpdated = (itemNo: number) => {
    const assig = findCurrentAssignment(props.session, props.assignments);
    if (assig && itemNo >= 0 && itemNo < assig.items.length) {
      const itemId = assig.items[itemNo];
      props.onItemIdChanged(itemId);
    }
  }

  return (
    <Stack tokens={{ childrenGap: 10 }}>
      <ProgressIndicator
        styles={{ itemName: { color: "white" } }}
        label={`${Math.round(progress * 10000) / 100}% Complete`}
        percentComplete={progress} />
      <NavigationBar
        currentItemNo={itemNo}
        onPrevTrial={onPrevTrial}
        onNextTrial={onNextTrial} />
      <Separator></Separator>
      <AssignmentList
        onAssignIdChanged={props.onAssignIdChanged}
        items={assign_items} />
    </Stack>
  )
}


