import React from 'react';
import { User, SenseAnnotSession, Assignments, Annotations, Assignment } from "../datatypes"
import { Stack } from '@fluentui/react';
import { LoginStatus } from './LoginStatus';
import { Session } from './Session';
import { Tasks } from './Tasks';
import { makeNewSession } from '../SenseAnnot/annot_updater';
import { findCurrentAssignment, findCurrentItemNo } from './controller_utils';

export interface IControllerData {
  user: User | null;
  session: SenseAnnotSession | null;
  assignments: Assignments;
  annotations: Annotations;
  onSessionUpdated: (session: SenseAnnotSession) => void;
  login_fn: () => void;
  logout_fn: () => void;
}

function Controller(props: IControllerData) {

  const onAssignIdChanged = (assignId: string) => {
    let assigns = props.assignments.filter((x)=>x.assignId == assignId);
    if(assigns.length > 0){
      let assign = assigns[0];
      let newSession = makeNewSession(props.session!, assignId, assign.items[0]);
      props.onSessionUpdated(newSession);
    }
  }

  const onItemIdChanged = (itemId: string) => {
    const assig = findCurrentAssignment(props.session, props.assignments);
    if (assig && assig.items.indexOf(itemId) >= 0) {
      let newSession = makeNewSession(props.session!, assig.assignId, itemId);
      props.onSessionUpdated(newSession);
    }
  }

  return (

    <Stack tokens={{ childrenGap: 5, padding: 10 }}>
      <LoginStatus user={props.user} login_fn={props.login_fn} logout_fn={props.logout_fn} />
      <Session session={props.session}></Session>
      <Tasks
        session={props.session}
        assignments={props.assignments}
        annotations={props.annotations}
        onItemIdChanged={onItemIdChanged}
        onAssignIdChanged={onAssignIdChanged}></Tasks>
    </Stack>
  )
}


export default Controller;