import { SenseAnnotSession, Assignment, Assignments } from "../datatypes";

export function findCurrentAssignment(
  session: SenseAnnotSession|null,
  assignments: Assignments): Assignment | null {
  
  if (session) {
    const curAssigId = session.currentAssignmentId;
    const curAssigs = assignments.filter((x) => x.assignId == curAssigId);
    if (curAssigs.length > 0) {
      return curAssigs[0];
    } else {
      return null;
    }
  }
  return null;
}

export function findCurrentItemNo(
  session: SenseAnnotSession|null,
  assignment: Assignment | null):number {

  const curItemId = session?.currentItemId;
  if (assignment && curItemId) {
    const curItemSerial = assignment.items.indexOf(curItemId);
    return curItemSerial;
  }

  return -1;
}